import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { SidebarComponent } from '../../shared/sidebar/sidebar.component';
// import { CreateSectionCollectionComponent } from './create-section-collection/create-section-collection.component';
import { EditSectionCollectionComponent } from './edit-section-collection/edit-section-collection.component';
import { SectionsCollectionComponent } from './index-section-collection/sections-collection.component';

const routes: Routes = [{
  path: '',
  component: SidebarComponent,
  children: [
    { path: '', component: SectionsCollectionComponent },
    { path: 'create-section-collection', component: EditSectionCollectionComponent },
    { path: 'edit-section-collection/:id', component: EditSectionCollectionComponent },
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class SectionsCollectionRoutingModule {
}
