import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { HomeSectionsService } from '../../../services/home-sections/home-sections.services';
import { Router } from '@angular/router';
import { NbDialogService, NbToastrService } from '@nebular/theme';
import { sectionColumn } from './section.column';
import { KendoCustomeGrideModel } from '../../../shared/kendo-custome-grid/model/kendoCustomeGrid.model';
import { GridDataResult } from '@progress/kendo-angular-grid';
@Component({
  selector: 'app-sections-collection',
  templateUrl: 'sections-collection.component.html',
  styleUrls: ['sections-collection.component.scss'],
})
export class SectionsCollectionComponent implements OnInit {

  @ViewChild('dialog') dialog: any;

  public SectionColumn: KendoCustomeGrideModel[] = sectionColumn;
  public isLoading: boolean = false;
  public dataItem: GridDataResult = {
    data: [],
    total: 0
  };
  public currentDeleteSections: any;
  constructor(
    private homeSectionService: HomeSectionsService,
    private toaster: NbToastrService,
    private router: Router,
    private dialogService: NbDialogService,
  ) {
  }

  ngOnInit() {
    this.getsections();
  }

  getsections() {
    this.homeSectionService.getSections().subscribe(
      res => {
        this.dataItem.data = res['data'];
        this.dataItem.total = res['data'].length;
      },
      error => { },
      () => { }
    );
  }

  deleteSection(id: any) {
    this.homeSectionService.deleteSection(id).subscribe(res => {
      this.toaster.show(res['msg'], 'Success', { status: 'success' });
    }, error => {
      this.toaster.show(error.error.message, 'Error', { status: 'danger' });
    });
  }

  commandActionClick(event: any) {
    switch (event.action.action) {
      case 'edit':
        this.router.navigate(['./sections-collection/edit-section-collection/' + event.data.id])
        break;

      case 'delete':
        this.currentDeleteSections = event.data;
        this.open(this.dialog);
        break;
    }
  }

  open(dialog: TemplateRef<any>) {
    this.dialogService.open(dialog);
  }

}
