import { Injectable } from '@angular/core';
import { environment } from '../../../environments/environment';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class HomeSectionsService {

  constructor(private http: HttpClient) {
  }

  public getSections() {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/home-sections';
    return this.http.get(url, {});
  }

  public getSection(id: any) {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/home-sections/' + id;
    return this.http.get(url);
  }

  public createSection(newSection: any) {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/home-sections';
    return this.http.post(url, newSection);
  }

  public updateSection(section: any) {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/home-sections/update';
    return this.http.post(url, section);
  }

  public deleteSection(id: any) {
    const url = environment.MAIN_SYSTEM + 'api/dashboard/home-sections/' + id;
    return this.http.delete(url);
  }


}
